<template>
  <div id="app">
    <!-- <nav class="navbar navbar-inverse navbar-fixed-top">
        <div class="container-fluid">
      <div class="navbar-header">
        <a class="navbar-brand" href="#"><h4>Fiber Map</h4></a>
      </div>
      <ul class="nav navbar-nav">
        <li class="active"><a href="#"><h4>Monitor</h4></a></li>
        <li><a href="#"><h4>Real Path</h4></a></li>
      </ul>
      <ul class="nav navbar-nav navbar-right">
        <li><a href="#"><h4><span class="glyphicon glyphicon-log-in"></span> Login</h4></a></li>
      </ul>
    </div>
     </nav> -->
   

    <!-- map -->
    <gmap-map :center="center" @zoom_changed="updateZoom" @center_changed="updateCenter" :zoom="zoom" style="width: 65%; height: 550px; float: left;">

<!-- Marker+InfoWindow @@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@ -->
    <gmap-info-window :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false" v-if="infoMarker">
        Name: {{infoContent.name}}<br>
        <!-- IP: {{infoContent.ip}}<br> -->
        <p v-if="infoContent.uptime=='0:00:00'">Status: Down <br> Uptime: 0:00:00 Hours</p>
        <p v-else>Status: Up <br> Uptime: {{ infoContent.uptime }} Hours</p>
         <a @click="zoomMap(infoWindowPos,'marker')">Zoom</a> <!--|| <a data-toggle="modal" data-target="#myModal">History</a> -->
    </gmap-info-window> 
    <gmap-marker
      :key="index"
      v-for="(m, index) in switchs"
      :title="m.name"
      :position="m.position"
      :clickable="true"
      :draggable="false"
      :icon="image.url"
      @click="toggleInfoWindow(m,index)"
    ></gmap-marker>


    <!-- <gmap-polyline
      :key="index"
      v-for="(p, index) in connects"
      :position="p.position"
      :path="p.path"
      :clickable="true"
      @click="toggleInfoWindow(pl,i)"
    ></gmap-polyline> -->
    <gmap-info-window  :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false" v-if="infoPolyline">
        From: {{ infoContent.A }} <br>
        Port: {{ infoContent.port_A }} <br>
        --------------------------------------------- <br>
        <p>To: {{ infoContent.B }} <br>
        Port: {{ infoContent.port_B }}</p>
        <a @click="zoomMap(infoWindowPos,'line')">Zoom</a> || <a data-toggle="modal" data-target="#History" @click="loadHistory">History</a> || 
        <a data-toggle="modal" data-target="#Problem"></a>
        || <a @click="focusLine(infoContent.id)">Focus</a> || <a @click="unfocusLine()">UnFocus</a> || <a @click="toRealpath()">RealPath</a> 
    </gmap-info-window> 
      <gmap-polyline
      v-for="(pl,index) in connects" 
      :key="index" 
      :path="pl.path" 
      :options="{geodesic:true, strokeColor:pl.linecolor ,strokeWeight:4}"
      @click="toggleInfoWindow2(pl,index)"
      @mousemove="posMouse">>
      </gmap-polyline>

      </gmap-map>


    <div style="display: inline-block; margin-left: 10px; width: 30%">
      <div id="bigBoxs">
        <!-- <input v-model="search" type="text" /> -->
        <!-- </div> -->
        <!-- <div class="col-md-6 col-md-offset-6" > -->
        <h2>Status :
          <!-- <input type="submit" class="btn btn-default" @click="checkStatus('all')" value="All"/>
          <input type="submit" class="btn btn-default" @click="checkStatus('up')" value="Up"/>
          <input type="submit" class="btn btn-default" @click="checkStatus('down')" value="Down"/> -->
        <div class="btn-group">
          <input type="submit" class="btn btn-default" @click="showByStatus('all')" value="All"/>
          <input type="submit" class="btn btn-default" @click="showByStatus('updown',1)" value="Up"/>
          <input type="submit" class="btn btn-default" @click="showByStatus('updown',0)" value="Down"/>
        </div>
        </h2>
        <!-- </div> -->
        <!-- <div class="col-md-6 col-md-offset-6" > -->
        <h2>Layer :
          <!-- <button class="btn btn-default" type="submit">All</button>
          <button class="btn btn-default" type="submit">Core-End</button>
          <button class="btn btn-default" type="submit">Core-Core</button> -->
          <div class="btn-group">
            <input type="submit" class="btn btn-default" @click="showLayer('all')" value="All"/>
            <input type="submit" class="btn btn-default" @click="showLayer('coretocore')" value="Core-Core"/>
            <input type="submit" class="btn btn-info dropdown-toggle" data-toggle="dropdown" value="Zone"/>
            <ul class="dropdown-menu dropdown-menu-right">
              <li><a @click="showLayer('zone',1)">COM</a></li>
              <li><a @click="showLayer('zone',2)">ENG</a></li>
              <li><a @click="showLayer('zone',5)">AGR</a></li>
              <li><a @click="showLayer('zone',4)">MED</a></li>
              <li><a @click="showLayer('zone',34)">MAE-HEA</a></li>
            </ul>   
          </div>
        </h2>
        <h2>Filtered By :
          <!-- <button class="btn btn-default" type="submit">All</button>
          <button class="btn btn-default" type="submit">Core-End</button>
          <button class="btn btn-default" type="submit">Core-Core</button> -->
          <div class="btn-group">
            <input type="submit" class="btn btn-default" @click="showTable('switch')" value="Switch"/>
            <input type="submit" class="btn btn-default" @click="showTable('line')" value="Optical Fiber"/>
          </div>
        </h2>
        <vue-good-table v-if="isTableMarker"
          :columns="columns"
          :rows="filterData"
          :paginate="true"
          :lineNumbers="false"
          :globalSearch="true"
          :onClick="toggleInfoWindow"/>
          <vue-good-table v-if="isTableLine"
          :columns="columnsLine"
          :rows="connects"
          :paginate="true"
          :lineNumbers="false"
          :globalSearch="true"
          :onClick="toggleInfoWindow2"/>
        </div>
      </div>

      <div class="modal" id="History" tabindex="-1" role="dialog" aria-labelledby="History" aria-hidden="true" style="width: 86%;">
        <div class="modal-dialog" role="document">
          <div class="modal-content" style="width: 140%;">
            <div class="modal-header">
              <h2 class="modal-title" id="History">History</h2>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div v-for="(h,index) in history" :key="index">
                <h4>{{ h.broken_time }} - Distance: {{ h.distance }} m - 
                  <font v-if="h.status_broken==0" color="green"> - {{ h.timestamp }} </font>
                  <font v-if="h.status_broken==1" color="red"></font>
                  <font v-if="h.status_broken==2" color="Orange"></font>
                  <font v-if="h.status_broken==1">
                    <label class="checkbox-inline"><input @click="chkBroken=2" type="radio" name="chkbroken" id="chkbroken"></label> 
                    <label class="checkbox-inline"><input @click="chkBroken=0" type="radio" name="chkbroken" id="chkbroken"></label> 
                    <button type="submit" class="btn btn-primary" @click="checkBroken(index)"></button>
                  </font>
                  <font v-if="h.status_broken==2">
                    <label class="checkbox-inline"><input @click="chkBroken=2" type="radio" name="chkbroken" id="chkbroken" disabled></label> 
                    <label class="checkbox-inline"><input @click="chkBroken=0" type="radio" name="chkbroken" id="chkbroken"></label> 
                    <button type="submit" class="btn btn-primary" @click="checkBroken(index)"></button>
                  </font>
                </h4>              
              </div>
            </div>
            <div class="modal-footer">
              <!--|| <label class="checkbox-inline"><input type="checkbox" value="" disabled></label> <label class="checkbox-inline"><input type="checkbox" value=""></label> <button type="button" class="btn btn-primary"></button> -->
              Time: <input type="date" name="bday"> - <input type="date" name="bday">
              <button type="button" class="btn btn-primary" @click="downloadHistory(history)">Export History</button>
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>
      
      <div class="modal" id="Problem" tabindex="-1" role="dialog" aria-labelledby="Problem" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h2 class="modal-title" id="Problem"></h2>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <h3> <input v-model="distance" type="text" placeholder=""/>  <button type="button" class="btn btn-primary" data-dismiss="modal" @click="saveHistory">Add</button></h3>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import CardLine1ChartExample from './dashboard/CardLine1ChartExample'
import CardLine2ChartExample from './dashboard/CardLine2ChartExample'
import CardLine3ChartExample from './dashboard/CardLine3ChartExample'
import CardBarChartExample from './dashboard/CardBarChartExample'
import MainChartExample from './dashboard/MainChartExample'
import SocialBoxChartExample from './dashboard/SocialBoxChartExample'
import CalloutChartExample from './dashboard/CalloutChartExample'
import { Callout } from '../components/'

import pdfMake from 'pdfmake/build/pdfmake'
import pdfFonts from 'pdfmake/build/vfs_fonts'
pdfMake.vfs = pdfFonts.pdfMake.vfs
pdfMake.fonts = {
  THSarabunNew: {
    normal: 'THSarabunNew.ttf',
    bold: 'THSarabunNew-Bold.ttf',
    italics: 'THSarabunNew-Italic.ttf',
    bolditalics: 'THSarabunNew-BoldItalic.ttf'
  },
  Roboto: {
    normal: 'Roboto-Regular.ttf',
    bold: 'Roboto-Medium.ttf',
    italics: 'Roboto-Italic.ttf',
    bolditalics: 'Roboto-MediumItalic.ttf'
  }
}

export default {
  name: 'dashboard',
  components: {
    Callout,
    CardLine1ChartExample,
    CardLine2ChartExample,
    CardLine3ChartExample,
    CardBarChartExample,
    MainChartExample,
    SocialBoxChartExample,
    CalloutChartExample
  },
  data () {
    return {
      image: {
        // url:'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png'
      },
      zoom: 16,
      center: { lat: 18.79686, lng: 98.9537475 },
      mousePos: { lat: 18.79686, lng: 98.9537475 },
      infoContent: '',
      infoMarker: false,
      infoPolyline: false,
      infoWindowPos: {
        lat: 0,
        lng: 0
      },
      infoWinOpen: false,
      currentMidx: null,
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      },
      markers: [
        {
          position: {
            lat: 47.376332,
            lng: 8.547511
          },
          infoText: 'Marker 1'
        },
        {
          position: {
            lat: 47.374592,
            lng: 8.548867
          },
          infoText: 'Marker 2'
        },
        {
          position: {
            lat: 47.379592,
            lng: 8.549867
          },
          infoText: 'Marker 3'
        }
      ],
      plPath: [
        { lat: 18.7956453, lng: 98.952812 },
        { lat: 18.7982302, lng: 98.9491877 },
        { lat: 18.8004538, lng: 98.9504027 },
        { lat: 18.7932254, lng: 98.9562057 },
        { lat: 18.7956453, lng: 98.952812 }
      ],
      lineColor: 'RED',
      ppath: [
        {
          path: [
            { lat: 18.803528, lng: 98.952944 },
            { lat: 18.803289, lng: 98.952839 },
            { lat: 18.803622, lng: 98.951974 },
            { lat: 18.801703, lng: 98.951205 },
            { lat: 18.800288, lng: 98.951428 },
            { lat: 18.799764, lng: 98.951980 },
            { lat: 18.799236, lng: 98.951806 },
            { lat: 18.798836, lng: 98.951308 },
            { lat: 18.79685, lng: 98.951251 },
            { lat: 18.79674, lng: 98.951974 },
            { lat: 18.796134, lng: 98.951974 },
            { lat: 18.796137, lng: 98.951652 }
          ]
        },
        {
          path: [
            { lat: 18.7982302, lng: 98.9491877 },
            { lat: 18.7956453, lng: 98.952812 }
          ]
        },
        {
          path: [
            { lat: 18.8004538, lng: 98.9504027 }
            // { lat: 18.7982302, lng: 98.9491877 }
          ]
        },
        {
          path: [
            { lat: 18.7932254, lng: 98.9562057 },
            { lat: 18.7932254, lng: 98.9562057 }
          ]
        }
      ],
      plvisible: true,
      switchs: [],
      connects: [],
      search: '',
      columns: [
        {
          label: 'Name',
          field: 'name',
          filterable: false
        },
        {
          label: 'Status',
          field: 'status',
          type: 'string',
          html: false,
          filterable: false
        },
        {
          label: 'Uptime',
          field: 'uptime',
          type: 'number',
          html: false,
          filterable: false
        }
      ],
      isTableMarker: true,
      columnsLine: [
        {
          label: 'From',
          field: 'A',
          filterable: false
        },
        {
          label: 'To',
          field: 'B',
          filterable: false
        },
        {
          label: 'Status',
          field: 'linestatus',
          html: false,
          filterable: false
        }
      ],
      isTableLine: false,
      distance: '',
      history: [{ comment: '' }],
      chkBroken: 1
    }
  },
  methods: {
    variant (value) {
      let $variant
      if (value <= 25) {
        $variant = 'info'
      } else if (value > 25 && value <= 50) {
        $variant = 'success'
      } else if (value > 50 && value <= 75) {
        $variant = 'warning'
      } else if (value > 75 && value <= 100) {
        $variant = 'danger'
      }
      return $variant
    },
    checkBroken (index) {
      // console.log(this.chkBroken)
      // console.log(this.history[index].status_broken)
      // console.log(this.history[index].distance)
      this.axios.get('https://fibermap.herokuapp.com/updatehistory', {
        params: {
          lineid: this.history[index].line_id,
          distance: this.history[index].distance,
          status_broken: this.chkBroken
        }
      })
        .then(response => {
          this.loadHistory()
        })
    },
    saveHistory () {
      this.axios.get('https://fibermap.herokuapp.com/savehistory', {
        params: {
          id: this.infoContent.id,
          A: this.infoContent.A,
          B: this.infoContent.B,
          distance: this.distance
        }
      })
      window.alert('')
      this.distance = ''
    },
    loadHistory () {
      this.axios
        .get('https://fibermap.herokuapp.com/loadhistory', {
          params: {
            id: this.infoContent.id
          }
        })
        .then(response => {
          this.history = response.data
          console.log(response.data)
        })
    },
    downloadHistory (history) {
      // console.log(JSON.stringify(value))
      var broken
      var distance
      var status
      var fixed
      for (var i = 0; i < history.length; i++) {
        if (broken === undefined) {
          broken = history[i].broken_time.toString()
          distance = history[i].distance.toString() + ' m'
          if (parseInt(history[i].status_broken) === 0) {
            status = ''
            fixed = history[i].timestamp.toString()
          } else if (parseInt(history[i].status_broken) === 1) {
            status = ''
            fixed = '-'
          } else if (parseInt(history[i].status_broken) === 2) {
            status = ''
            fixed = '-'
          }
        } else {
          broken = broken + '\n' + history[i].broken_time.toString()
          distance = distance + '\n' + history[i].distance.toString() + ' m'
          if (parseInt(history[i].status_broken) === 0) {
            status = status + '\n' + ''
            fixed = fixed + '\n' + history[i].timestamp.toString()
          } else if (parseInt(history[i].status_broken) === 1) {
            status = status + '\n' + ''
            fixed = fixed + '\n' + '-'
          } else if (parseInt(history[i].status_broken) === 2) {
            status = status + '\n' + ''
            fixed = fixed + '\n' + '-'
          }
        }
      }
      var docDefinition = {
        content: [
          {text: 'History', style: 'header', fontSize: 30},
          history[0].comment,
          {
            layout: 'lightHorizontalLines', // optional
            table: {
              // headers are automatically repeated if the table spans over multiple pages
              // you can declare how many rows should be treated as headers
              headerRows: 1,
              widths: [ 'auto', 'auto', 'auto', 'auto' ],

              body: [
                [ '', '', '', '' ],
                [ broken, distance, status, fixed ]
              ]
            }
          }
        ],
        defaultStyle: {
          font: 'THSarabunNew',
          fontSize: 18
        }
      }
      pdfMake.createPdf(docDefinition).download()
    },
    showLayer (value, e) {
      if (value === 'all') {
        this.getSwitch()
        this.getConnect()
      }
      if (value === 'coretocore') {
        this.axios
          .get('https://fibermap.herokuapp.com/showlayer')
          .then(response => {
            // console.log(response.data[0].switch);
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
          })
      }
      if (value === 'zone') {
        console.log(e)
        this.axios
          .get('https://fibermap.herokuapp.com/showzone', {
            params: {
              zone: e
            }
          })
          .then(response => {
            this.center = response.data[0].switch[0].position
            console.log(response.data[0])
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
          })
      }
    },
    focusLine (value) {
      this.axios
        .get('https://fibermap.herokuapp.com/connectbyid', {
          params: {
            id: value
          }
        })
        .then(response => {
          console.log(response.data[0].switch)
          this.switchs = response.data[0].switch
          this.connects = response.data[0].connect
        })
    },
    unfocusLine () {
      this.getSwitch()
      this.getConnect()
    },
    toRealpath () {
      console.log(this.$route)
      // this.$route.go(1)
      location.href = 'http://localhost:8080/#/maptoreal?lineid=' + this.infoContent.id + '&lat=' + this.mousePos.lat + '&lng=' + this.mousePos.lng
      // console.log(this.infoContent.id)
    },
    zoomMap (pos, obj) {
      if (obj === 'marker') {
        this.center = pos
        this.zoom = 18
      } else if (obj === 'line') {
        this.center = pos
        this.zoom = 17
      }
    },
    updateZoom (zoom) {
      // console.log(zoom)
      this.zoom = zoom
    },
    updateCenter (center) {
      this.center = {
        lat: center.lat(),
        lng: center.lng()
      }
    },
    posMouse (pos) {
      this.mousePos = {
        lat: pos.latLng.lat(),
        lng: pos.latLng.lng()
      }
    },
    showTable (value) {
      // console.log(value)
      if (value === 'switch') {
        this.isTableMarker = true
        this.isTableLine = false
      } else if (value === 'line') {
        this.isTableMarker = false
        this.isTableLine = true
      }
    },
    showByStatus (value, e) {
      // console.log(value)
      // console.log(e)
      if (value === 'all') {
        this.getSwitch()
        this.getConnect()
      }
      if (value === 'updown') {
        this.axios
          .get('https://fibermap.herokuapp.com/showbystatus', {
            params: {
              status: e
            }
          })
          .then(response => {
            // console.log(response.data[0].switch);
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
          })
      }
    },
    toggleInfoWindow (marker, idx) {
      this.infoOptions.pixelOffset.height = -35
      this.infoWindowPos = marker.position
      this.infoContent = marker
      this.center = marker.position
      this.infoWinOpen = true
      this.currentMidx = idx
      this.infoMarker = true
      this.infoPolyline = false
    },
    toggleInfoWindow2 (line, idx) {
      this.infoOptions.pixelOffset.height = 0
      // var midLat = (line.path[0].lat + line.path[1].lat) / 2
      // var midLng = (line.path[0].lng + line.path[1].lng) / 2
      // console.log(line)
      this.infoWindowPos = { lat: this.mousePos.lat, lng: this.mousePos.lng }
      this.infoContent = line
      // this.center = { lat: midLat, lng: midLng }
      this.infoWinOpen = true
      this.currentMidx = idx
      this.infoPolyline = true
      this.infoMarker = false
    },
    getSwitch () {
      this.axios
        .get('https://fibermap.herokuapp.com/getswitch')
        .then(response => {
          // console.log(response.data[0].name)
          this.switchs = response.data
        })
    },
    getConnect () {
      this.axios
        .get('https://fibermap.herokuapp.com/connect')
        .then(response => {
          // console.log(response.data)
          this.connects = response.data
        })
    }
  },
  created: function () {
    this.getSwitch()
    this.getConnect()
  },
  computed: {
    filterData () {
      return this.switchs.filter(sw => {
        return sw.name.match(this.search)
      })
    }
  }
}
</script>



