<template>
  <div id="app">
    <!-- map -->
    <gmap-map :center="center" 
    @zoom_changed="updateZoom" 
    @center_changed="updateCenter"
    :zoom="zoom"
    mapTypeId="hybrid"
    style="width: 65%; height: 550px; float: left;">
    
<!-- Marker+InfoWindow @@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@ -->
    <gmap-info-window :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false" v-if="infoMarker">
        Name: {{infoContent.name}}<br>
        <!-- IP: {{infoContent.ip}}<br> -->
        <p v-if="infoContent.uptime=='0:00:00'">Status: Down <br> Uptime: 0:00:00 Hours</p>
        <p v-else>Status: Up <br> Uptime: {{ infoContent.uptime }} Hours</p>
         <a @click="zoomMap(infoWindowPos,'marker')">Zoom</a> <!--|| <a data-toggle="modal" data-target="#myModal">History</a> -->
    </gmap-info-window> 
    <gmap-marker
      :key="index"
      v-for="(m, index) in filterData"
      :title="m.name"
      :position="m.position"
      :clickable="true"
      :draggable="false"
      :icon="image.url"
      @click="toggleInfoWindow(m,index)"
    ></gmap-marker>

    <gmap-marker
      :key="index"
      v-for="(m, index) in markers"
      :title="m.name"
      :position="m.position"
      :clickable="true"
      :draggable="false"
      :icon="image1.url"
      @click="toggleInfoWindow(m,index)">
      </gmap-marker>

    <gmap-info-window  :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false" v-if="infoPolyline">
        From: {{ infoContent.A }} <br>
        Port: {{ infoContent.port_A }} <br>
        --------------------------------------------- <br>
        <p>To: {{ infoContent.B }} <br>
        Port: {{ infoContent.port_B }}</p>
        <a @click="zoomMap(infoWindowPos,'line')">Zoom</a> || <a data-toggle="modal" data-target="#History" @click="loadHistory">History</a> || 
        <a data-toggle="modal" data-target="#Problem"></a>
        || <a @click="focusLine(infoContent.id)">Focus</a> || <a @click="unfocusLine()">UnFocus</a> || <a @click="routeStreetView">Street View</a>
    </gmap-info-window> 
    <gmap-polyline
      v-for="(pl,index) in connects" 
      :key="index" 
      :path="pl.path" 
      :options="{geodesic:true, strokeColor:pl.linecolor ,strokeWeight:4, icons: [{icon: pl.icon, offset: pl.broken_percentile}]}"
      @click="toggleInfoWindow2(pl,index)"
      @mousemove="posMouse">
    </gmap-polyline>
    
    </gmap-map>

    <button @click="routeStreetView">EEE</button>

    <div style="display: inline-block; margin-left: 10px; width: 30%">
      <div id="bigBoxs">
        <!-- <input v-model="search" type="text" /> -->
        <!-- </div> -->
        <!-- <div class="col-md-6 col-md-offset-6" > -->
        <h2>Status :
          <!-- <input type="submit" class="btn btn-default" @click="checkStatus('all')" value="All"/>
          <input type="submit" class="btn btn-default" @click="checkStatus('up')" value="Up"/>
          <input type="submit" class="btn btn-default" @click="checkStatus('down')" value="Down"/> -->
        <div class="btn-group">
          <input type="submit" class="btn btn-default" @click="showByStatus('all')" value="All"/>
          <input type="submit" class="btn btn-default" @click="showByStatus('updown',1)" value="Up"/>
          <input type="submit" class="btn btn-default" @click="showByStatus('updown',0)" value="Down"/>
        </div>
        </h2>
        <!-- </div> -->
        <!-- <div class="col-md-6 col-md-offset-6" > -->
        <h2>Layer :
          <!-- <button class="btn btn-default" type="submit">All</button>
          <button class="btn btn-default" type="submit">Core-End</button>
          <button class="btn btn-default" type="submit">Core-Core</button> -->
          <div class="btn-group">
            <input type="submit" class="btn btn-default" @click="showLayer('all')" value="All"/>
            <input type="submit" class="btn btn-default" @click="showLayer('coretocore')" value="Core-Core"/>
            <input type="submit" class="btn btn-info dropdown-toggle" data-toggle="dropdown" value="Zone"/>
            <ul class="dropdown-menu dropdown-menu-right">
              <li><a @click="showLayer('zone',1)">COM</a></li>
              <li><a @click="showLayer('zone',2)">ENG</a></li>
              <li><a @click="showLayer('zone',5)">AGR</a></li>
              <li><a @click="showLayer('zone',4)">MED</a></li>
              <li><a @click="showLayer('zone',34)">MAE-HEA</a></li>
            </ul>   
          </div>
        </h2>
        <h2>Filtered By :
          <!-- <button class="btn btn-default" type="submit">All</button>
          <button class="btn btn-default" type="submit">Core-End</button>
          <button class="btn btn-default" type="submit">Core-Core</button> -->
          <div class="btn-group">
            <input type="submit" class="btn btn-default" @click="showTable('switch')" value="Switch"/>
            <input type="submit" class="btn btn-default" @click="showTable('line')" value="Optical Fiber"/>
          </div>
        </h2>
        <vue-good-table v-if="isTableMarker"
          :columns="columns"
          :rows="filterData"
          :paginate="true"
          :lineNumbers="false"
          :globalSearch="true"
          :onClick="toggleInfoWindow"/>
          <vue-good-table v-if="isTableLine"
          :columns="columnsLine"
          :rows="connects"
          :paginate="true"
          :lineNumbers="false"
          :globalSearch="true"
          :onClick="toggleInfoWindow2"/>
        </div>
      </div>

      <div class="modal" id="History" tabindex="-1" role="dialog" aria-labelledby="History" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h2 class="modal-title" id="History">History</h2>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div v-for="(h,index) in history" :key="index">
                <h3>{{ h.timestamp }} - Distance: {{ h.distance }} m </h3>
                  <!-- - <font v-if="h.status_broken==1" color="red"></font> <font v-if="h.status_broken==0" color="black"></font></p>  -->
                <!-- <div v-if="h.status_broken==1"><label class="checkbox-inline"><input type="checkbox" value="" disabled></label> <label class="checkbox-inline"><input type="checkbox" value=""></label> <button type="button" class="btn btn-primary"></button></div> -->
              </div>
            </div>
            <div class="modal-footer">
              <!--|| <label class="checkbox-inline"><input type="checkbox" value="" disabled></label> <label class="checkbox-inline"><input type="checkbox" value=""></label> <button type="button" class="btn btn-primary"></button> -->
              Time: <input type="date" name="bday"> - <input type="date" name="bday">
              <button type="button" class="btn btn-primary">Export History</button>
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>
      
      <div class="modal" id="Problem" tabindex="-1" role="dialog" aria-labelledby="Problem" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h2 class="modal-title" id="Problem"></h2>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <h3> <input v-model="distance" type="text" placeholder=""/>  <button type="button" class="btn btn-primary" data-dismiss="modal" @click="saveHistory">Add</button></h3>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>

      <!-- <div class="modal" id="Panorama" tabindex="-1" role="dialog" aria-labelledby="Panorama" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h2 class="modal-title" id="Panorama">Street View</h2>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <gmap-street-view-panorama id="panorama"
                class="pano"
                style="width: 100%; height: 400px;"
                :position="mousePos"
                :pov="{
                  heading: 0,
                  pitch: 0
                }"
                :motionTracking="true"
                :visible="panoOpen"
                @status_changed="updatePano"
                :visible_changed="updatePano">
              </gmap-street-view-panorama>
              <button @click="panoOpen=true">panoOpen</button> <button @click="panoOpen=false">panoClose</button>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="panoOpen=false">Close</button>
            </div>
          </div>
        </div>
      </div> -->
    </div>
</template>

<script>
import CardLine1ChartExample from './dashboard/CardLine1ChartExample'
import CardLine2ChartExample from './dashboard/CardLine2ChartExample'
import CardLine3ChartExample from './dashboard/CardLine3ChartExample'
import CardBarChartExample from './dashboard/CardBarChartExample'
import MainChartExample from './dashboard/MainChartExample'
import SocialBoxChartExample from './dashboard/SocialBoxChartExample'
import CalloutChartExample from './dashboard/CalloutChartExample'
import { Callout } from '../components/'
import funcJS from '../function.js'

export default {
  name: 'dashboard',
  components: {
    Callout,
    CardLine1ChartExample,
    CardLine2ChartExample,
    CardLine3ChartExample,
    CardBarChartExample,
    MainChartExample,
    SocialBoxChartExample,
    CalloutChartExample
  },
  data () {
    return {
      image: {
        // url: 'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png'
      },
      image1: {
        url: 'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png'
      },
      goldStar: {
        path: 'M-0.1,0a0.1,0.1 0 1,0 0.2,0a0.1,0.1 0 1,0 -0.2,0 M-6.5,0a6.5,6.5 0 1,0 13,0a6.5,6.5 0 1,0 -13,0',
        strokeColor: 'gold',
        strokeWeight: 3
      },
      broke_distace: 105,
      percentile: ['0%', '100%'],
      zoom: 16,
      center: { lat: 18.79686, lng: 98.9537475 },
      mousePos: { lat: 18.796861024696568, lng: 98.95122811930116 },
      infoContent: '',
      infoMarker: false,
      infoPolyline: false,
      infoWindowPos: {
        lat: 0,
        lng: 0
      },
      infoWinOpen: false,
      currentMidx: null,
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      },
      markers: [
        {
          position: {
            lat: 47.376332,
            lng: 8.547511
          },
          infoText: 'Marker 1'
        },
        {
          position: {
            lat: 47.374592,
            lng: 8.548867
          },
          infoText: 'Marker 2'
        },
        {
          position: {
            lat: 47.379592,
            lng: 8.549867
          },
          infoText: 'Marker 3'
        }
      ],
      plPath: [
        { lat: 18.7956453, lng: 98.952812 },
        { lat: 18.7982302, lng: 98.9491877 },
        { lat: 18.8004538, lng: 98.9504027 },
        { lat: 18.7932254, lng: 98.9562057 },
        { lat: 18.7956453, lng: 98.952812 }
      ],
      lineColor: 'RED',
      ppath: [
        {
          path: [
            { lat: 18.803528, lng: 98.952944 },
            { lat: 18.803289, lng: 98.952839 },
            { lat: 18.803622, lng: 98.951974 },
            { lat: 18.801703, lng: 98.951205 },
            { lat: 18.800288, lng: 98.951428 },
            { lat: 18.799764, lng: 98.951980 },
            { lat: 18.799236, lng: 98.951806 },
            { lat: 18.798836, lng: 98.951308 },
            { lat: 18.79685, lng: 98.951251 },
            { lat: 18.79674, lng: 98.951974 },
            { lat: 18.796134, lng: 98.951974 },
            { lat: 18.796137, lng: 98.951652 }
          ]
        },
        {
          path: [
            { lat: 18.7982302, lng: 98.9491877 },
            { lat: 18.7956453, lng: 98.952812 }
          ]
        },
        {
          path: [
            { lat: 18.8004538, lng: 98.9504027 }
            // { lat: 18.7982302, lng: 98.9491877 }
          ]
        },
        {
          path: [
            { lat: 18.7932254, lng: 98.9562057 },
            { lat: 18.7932254, lng: 98.9562057 }
          ]
        }
      ],
      plvisible: true,
      switchs: [],
      connects: [],
      search: '',
      columns: [
        {
          label: 'Name',
          field: 'name',
          filterable: false
        },
        {
          label: 'Status',
          field: 'status',
          type: 'string',
          html: false,
          filterable: false
        },
        {
          label: 'Uptime',
          field: 'uptime',
          type: 'number',
          html: false,
          filterable: false
        }
      ],
      isTableMarker: true,
      columnsLine: [
        {
          label: 'From',
          field: 'A',
          filterable: false
        },
        {
          label: 'To',
          field: 'B',
          filterable: false
        },
        {
          label: 'Status',
          field: 'linestatus',
          html: false,
          filterable: false
        }
      ],
      isTableLine: false,
      distance: '',
      history: [{ comment: '' }],
      myLocation: '',
      panoOpen: true
    }
  },
  methods: {
    hellojs: funcJS.hello,
    variant (value) {
      let $variant
      if (value <= 25) {
        $variant = 'info'
      } else if (value > 25 && value <= 50) {
        $variant = 'success'
      } else if (value > 50 && value <= 75) {
        $variant = 'warning'
      } else if (value > 75 && value <= 100) {
        $variant = 'danger'
      }
      return $variant
    },
    showBroken () {
      // console.log(this.connects)
      // this.goldStar.path = 'M-0.1,0a0.1,0.1 0 1,0 0.2,0a0.1,0.1 0 1,0 -0.2,0 M-6.5,0a6.5,6.5 0 1,0 13,0a6.5,6.5 0 1,0 -13,0'
      // this.goldStar.strokeColor = 'blue'
      // this.goldStar.strokeWeight = 3
      this.axios
        .get('https://fibermap.herokuapp.com/getbroken')
        .then(response => {
          this.broke_distace = response.data
          // console.log(this.broke_distace.length)
          for (var i = 0; i < this.broke_distace.length; i++) {
            for (var j = 0; j < this.connects.length; j++) {
              if (this.broke_distace[i].line_id === this.connects[j].id) {
                this.connects[j]['broke_distance'] = this.broke_distace[i].distance
                this.connects[j]['icon'] = {'path': 'M-0.1,0a0.1,0.1 0 1,0 0.2,0a0.1,0.1 0 1,0 -0.2,0 M-6.5,0a6.5,6.5 0 1,0 13,0a6.5,6.5 0 1,0 -13,0', 'strokeColor': 'gold', 'strokeWeight': 3}
              } else if (this.connects[j].broke_distance === undefined) {
                this.connects[j]['broke_distance'] = 0
                this.connects[j]['icon'] = {'path': '', 'strokeColor': 'gold', 'strokeWeight': 3}
              }
            }
          }
          this.calDistance()
        })
    },
    calDistance () {
      for (var x = 0; x < this.connects.length; x++) {
        var distance = 0
        for (var i = 0; i < this.connects[x].path.length - 1; i++) {
          distance = distance + this.getDistance(this.connects[x].path[i].lat, this.connects[x].path[i].lng, this.connects[x].path[i + 1].lat, this.connects[x].path[i + 1].lng)
        }
        this.percentile = ((this.connects[x].broke_distance / 1000) * 100) / distance
        this.percentile = this.percentile.toString() + '%'
        this.connects[x]['broken_percentile'] = this.percentile
        // console.log(this.connects[x].broke_distance)
      }
      // console.log(this.connects[1].broke_distance)
    },
    getDistance (slat, slng, flat, flng) {
      var R = 6371
      var dLat = (flat - slat) * (Math.PI / 180)
      var dLng = (flng - slng) * (Math.PI / 180)
      var a =
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(slat * (Math.PI / 180)) * Math.cos(flat * (Math.PI / 180)) *
        Math.sin(dLng / 2) * Math.sin(dLng / 2)
      var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a))
      var d = R * c
      return d
      // console.log(d)
    },
    saveHistory () {
      this.axios.get('https://fibermap.herokuapp.com/savehistory', {
        params: {
          id: this.infoContent.id,
          A: this.infoContent.A,
          B: this.infoContent.B,
          distance: this.distance
        }
      })
      window.alert('')
      this.distance = ''
    },
    loadHistory () {
      this.axios
        .get('https://fibermap.herokuapp.com/loadhistory', {
          params: {
            id: this.infoContent.id
          }
        })
        .then(response => {
          this.history = response.data
          console.log(response.data)
        })
    },
    showLayer (value, e) {
      if (value === 'all') {
        this.getSwitch()
        this.getConnect()
      }
      if (value === 'coretocore') {
        this.axios
          .get('https://fibermap.herokuapp.com/showlayer')
          .then(response => {
            // console.log(response.data[0].switch);
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
          })
      }
      if (value === 'zone') {
        console.log(e)
        this.axios
          .get('https://fibermap.herokuapp.com/showzone', {
            params: {
              zone: e
            }
          })
          .then(response => {
            this.center = response.data[0].switch[0].position
            // console.log(response.data[0])
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
          })
      }
    },
    focusLine (value) {
      this.axios
        .get('https://fibermap.herokuapp.com/connectbyid', {
          params: {
            id: value
          }
        })
        .then(response => {
          console.log(response.data[0].switch)
          this.switchs = response.data[0].switch
          this.connects = response.data[0].connect
        })
    },
    unfocusLine () {
      this.getSwitch()
      this.getConnect()
    },
    zoomMap (pos, obj) {
      if (obj === 'marker') {
        this.center = pos
        this.zoom = 18
      } else if (obj === 'line') {
        this.center = pos
        this.zoom = 17
      }
    },
    updateZoom (zoom) {
      // console.log(zoom)
      this.zoom = zoom
    },
    updateCenter (center) {
      this.center = {
        lat: center.lat(),
        lng: center.lng()
      }
    },
    routeStreetView () {
      // location.href = 'http://maps.google.com/maps?q=&layer=c&cbll=' + this.mousePos.lat + ',' + this.mousePos.lng
      window.open('http://maps.google.com/maps?q=&layer=c&cbll=' + this.mousePos.lat + ',' + this.mousePos.lng, '_blank')
    },
    posMouse (pos) {
      this.mousePos = {
        lat: pos.latLng.lat(),
        lng: pos.latLng.lng()
      }
    },
    showTable (value) {
      // console.log(value)
      if (value === 'switch') {
        this.isTableMarker = true
        this.isTableLine = false
      } else if (value === 'line') {
        this.isTableMarker = false
        this.isTableLine = true
      }
    },
    showByStatus (value, e) {
      // console.log(value)
      // console.log(e)
      if (value === 'all') {
        this.getSwitch()
        this.getConnect()
      }
      if (value === 'updown') {
        this.axios
          .get('https://fibermap.herokuapp.com/showbystatus', {
            params: {
              status: e
            }
          })
          .then(response => {
            // console.log(response.data[0].switch);
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
          })
      }
    },
    toggleInfoWindow (marker, idx) {
      this.infoOptions.pixelOffset.height = -35
      this.infoWindowPos = marker.position
      this.infoContent = marker
      this.center = marker.position
      this.infoWinOpen = true
      this.currentMidx = idx
      this.infoMarker = true
      this.infoPolyline = false
    },
    toggleInfoWindow2 (line, idx) {
      this.infoOptions.pixelOffset.height = 0
      // var midLat = (line.path[0].lat + line.path[1].lat) / 2
      // var midLng = (line.path[0].lng + line.path[1].lng) / 2
      console.log(this.mousePos)
      this.infoWindowPos = { lat: this.mousePos.lat, lng: this.mousePos.lng }
      this.infoContent = line
      // this.center = { lat: this.mousePos.lat, lng: this.mousePos.lng }
      this.infoWinOpen = true
      this.currentMidx = idx
      this.infoPolyline = true
      this.infoMarker = false
    },
    getSwitch () {
      this.axios
        .get('https://fibermap.herokuapp.com/getswitch')
        .then(response => {
          // console.log(response.data[0].name)
          this.switchs = response.data
        })
    },
    getConnect () {
      this.axios
        .get('https://fibermap.herokuapp.com/getrealpath')
        .then(response => {
          this.connects = response.data
          this.showBroken()
          // this.calDistance()
          // console.log(this.connects[0].path)
          // console.log(this.connects)
        })
    }
  },
  created: function () {
    this.getSwitch()
    this.getConnect()
    var eiei = this.$route.query.lineid
    console.log(parseInt(eiei))
    console.log(this.$route.query.lineid)
  },
  computed: {
    filterData () {
      return this.switchs.filter(sw => {
        return sw.name.match(this.search)
      })
    }
  },
  mounted: function () {
    if (navigator.geolocation) {
      var self = this
      navigator.geolocation.getCurrentPosition(function (position) {
        self.myLocation = position.coords
        self.markers[0].position.lat = position.coords.latitude
        self.markers[0].position.lng = position.coords.longitude
        // console.log(position.coords.latitude)
        // console.log(position.coords.longitude)
        // console.log(self.myLocation)
      })
    }
  }
}
</script>



